.tiles {
    padding-top: 100px;
    grid-template-columns: repeat(2, 50%);
    grid-template-columns: 49.5% 1% 49.5%;
    background-color: #fff;
    .spacerH {
        grid-column: 2/3;
        grid-row: 2/5; }
    .head {
        text-align: center;
        grid-column: 1/-1;
        .title {}
        .subtitle {
            font-family: Raleway-SemiBold;
            text-transform: uppercase; }
        .separator {
            margin: auto;
            margin-top: 9px; }
        .additionally {
            margin: {}
            margin: 36px auto;
            font-family: MyriadPro-Regular;
            font-size: 20px;
            max-width: 750px; } }
    .description {
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        margin-bottom: auto;
        margin-top: auto;
        line-height: 21px;
        p {
            margin: 20px 0; }
        .wrapper {
            margin-left: auto;
            max-width: calc(1140px/2); }
        &.right {
            margin-right: auto;
            margin-left: 0; } }

    .img {
        background-size: cover;
        width: 100%;
        height: 545px;
        @include rmin(2560) {
            height: 760px; } } }
